.title {
  font-size: 23px;
  font-weight: 700;
  color: #f5f5f5
}

.daily-item {
  background-color: #f5f5f5;
  border-radius: 15px;
  height: 40px;
  margin: 5px 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  padding: 5px 20px;
}

.icon-small {
  width: 40px;
}

.day {
  color: #212121;
  flex: 1 1;
  font-weight: 600;
  margin-left: 15px;
}

.description {
  flex: 1 1;
  margin-right: 15px;
  text-align: right;
}

.min-max {
  color: #757575;
  font-weight: 600;
}

.daily-details-grid {
  background-color: #f5f5f5;
  border-radius: 15px;
  margin: 5px 0;

  grid-row-gap: 0;
  grid-column-gap: 15px;
  row-gap: 0px;
  column-gap: 15px;
  display: grid;
  flex: 1 1;
  grid-template-columns: auto auto;
  padding: 5px 15px;
}

.daily-details-grid-item {
  display: flex;
  height: 30px;
  justify-content: space-between;
  align-items: center;
}

.daily-details-grid-item label:first-child {
  color: #464646;
}

.daily-details-grid-item label:last-child {
  color: #212121;
}

@media (max-width: 400px) {
  .daily-item {
    margin: 4px 0;
    font-size: 12px;
  }

  .daily-details-grid {
    margin: 4px 0;
    grid-column-gap: 10px;
    column-gap: 10px;
    padding: 4px 12px;
    
    font-size: 14px;
  }
}
